.greet-wrap {
	position: relative;
	height: 100%;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
}

.greet-parallax {
	position: absolute;
	width: 110%;
	height: 110%;
	background: url('../img/bg/hello-bg.jpg') center center no-repeat;
	background-size: cover;
}

.greet {
	width: 300px;
	position: relative;
	animation: greet .4s linear;
}

.greet__user {
	display: flex;
	justify-content: center;
	align-items: flex-end;
	height: 155px;
	background-color: #fff;
	padding-bottom: 20px;
	border-radius: 10px 10px 0 0;
}

.greet__photo {
	position: absolute;
	top: -70px;
	left: 50%;
	transform: translateX(-50%);
	width: 140px;
	height: 140px;
	border-radius: 50%;
	border: 2px solid rgba($gray, .6);
	overflow: hidden;
}

.greet__img {
	width: auto;
	height: 100%;
	display: block;
	margin-left: -47px;
}

.greet__name {
	font-family: $firaMedium;
	font-size: 21px;
	text-align: center;
	max-width: 150px;

}

.greet__over {
	background-color: $blue;
	padding: 20px 0;
	border-radius: 0 0 10px 10px;
}

.greet__btn {
	display: block;
	width: 140px;
	font-family: $firaBold;
	font-size: 14px;
	color: #fff;
	border: 2px solid #fff;
	padding: 8px 13px;
	border-radius: 20px;
	margin: 0 auto;
	background: linear-gradient(to right, rgba(237,213,0,1) 
							0%,rgba(0,229,255,1) 
							30%,rgba(255,0,148,1) 
							60%,rgba(255,157,0,1) 90%);
	background-size: 200%;
	animation: hello-btn 6s infinite linear;
}

@keyframes hello-btn {
	0% {
		background-position-x: 0;
	}

	100% {
		background-position-x: 200%;
	}
}

@keyframes greet {
	0% {
		transform: scale(0);
	}

	100% {
		transform: scale(1);
	}
}