.header {
  	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2);
}

.container__header {
	justify-content: space-between;
	align-items: center;
	padding: 9px 0;

	@include phone {
		flex-direction: column;
	}
}

.user {
	@include phone {
		margin-bottom: 15px;
	}
}

.user__pic {
	width: 62px;
	height: 62px;
	border-radius: 50%;
	overflow: hidden;
	border: 2px solid $gray;
	display: inline-block;
	vertical-align: middle;
}

.user__img {
	display: block;
	margin-left: -20px;
	width: auto;
	height: 100%;
}

.user__name {
	display: inline-block;
	vertical-align: middle;
	margin-left: 19px;
	max-width: 160px;
}

.connect {
	display: flex;
	align-items: center;
}

.contacts {
	position: relative;
	margin-left: 15px;

	&::after {
		content: '';
		display: block;
		width: 100%;
		height: 20px;
		position: absolute;
		top: 100%;
	}

	&:hover {

		.contacts__dropdown {
			left: 50%;
		}
	}
}

.contacts__button {
	font-family: $firaBold;
	color: transparent;
	background: linear-gradient(to right, rgba(237,213,0,1) 
							0%,rgba(0,229,255,1) 
							30%,rgba(255,0,148,1) 
							60%,rgba(255,157,0,1) 90%);
	background-clip: text;
	background-size: 200%;
	animation: hello-btn 6s infinite linear;
	transition: color .3s;
}

.contacts__dropdown {
	position: absolute;
	top: 100%;
	left: -9999px;
	transform: translateX(-50%);
	margin-top: 14px;
	z-index: 10;
}

.contacts__list {
	border: 2px solid rgba($gray, .1);
	border-radius: 15px;
	background-color: #fff;

	&::before {
		content: '';
		display: block;
		position: absolute;
		top: -10px;
		left: 50%;
		transform: translateX(-50%);
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 6px 10px 6px;
		border-color: transparent transparent rgba($gray, .1) transparent;
	}

	&::after {
		content: '';
		display: block;
		position: absolute;
		top: -6px;
		left: 50%;
		transform: translateX(-50%);
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 6px 10px 6px;
		border-color: transparent transparent #fff transparent;
	}
}

.contacts__item {
	border-bottom: 2px solid rgba($gray, .1);
	padding: 15px 20px;

	&:last-child {
		border: none;
	}
}

.contacts__link {
	display: flex;
	align-items: center;

	&:hover {
		.contacts__name {
			color: rgba($black, .6);
		}

		.contacts__icon {
			color: rgba($black, .6);
		}
	}
}

.contacts__name {
	transition: .3s;
}

.contacts__icon {
	color: $blue;
	margin-right: 9px;
	width: 19px;
	transition: .3s;
}

.social__list {
	display: block;
	position: relative;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background-color: $blue;
	cursor: pointer;
	transition: .3s;

	&:hover {
		animation: none;
		width: 120px;
		border-radius: 20px;

		.social__first {
			opacity: 0;
		}

		.social__item {
			opacity: 1;
		}

		.vk {
			right: 50%;
			transform: translateX(50%);
		}

		.fb {
			right: 100%;
			transform: translateX(100%);
		}
	}
}

@keyframes squ {
	0% {
		border-radius: 50%;
		transform: rotate(0deg);
	}

	50% {
		border-radius: 0;
		transform: rotate(360deg);
	}

	100% {
		border-radius: 50%;
		transform: rotate(0deg);
	}
}

.social__first {
	position: absolute;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background-color: $blue;
	text-align: center;
	animation: squ 5s infinite;
}

.social__item {
	display: inline-block;
	position: absolute;
	top: 0;
	right: 0;
	transition-property: right, opacity;
	transition-duration: .4s, 1s;
	opacity: 0;
}

.social__link {
	display: block;
	position: relative;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background-color: #9c9c9c;
	transition: .3s;

	&:hover {
		background-color: #5d99c3;
	}
}

.social__icon, .social__network {
	position: absolute;
	width: 15px;
	height: 15px;
	fill: $black;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
