@font-face {
    font-family: 'firaBold';
    src: url('../fonts/fira/firasans-bold-webfont.woff2') format('woff2'),
         url('../fonts/fira/firasans-bold-webfont.woff') format('woff');
    font-weight: 700;
    font-style: normal;

}

