.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "firaRegular", sans-serif;
  font-size: 18px;
  color: #414141;
  line-height: 1.42;
}

ul,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0px;
  margin: 0px;
}

p {
  margin: 0;
}

* {
  box-sizing: border-box;
}

a {
  color: #414141;
  text-decoration: none;
  outline: none;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container {
  width: 96%;
  max-width: 940px;
  margin: 0 auto;
  display: flex;
}

.content {
  background-color: #f6f6f6;
  height: 100%;
}

.title {
  display: inline-block;
  font-family: "firaMedium", sans-serif;
  font-size: 35px;
  margin-bottom: 25px;
}

/* block */

.greet-wrap {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.greet-parallax {
  position: absolute;
  width: 110%;
  height: 110%;
  background: url("../img/bg/hello-bg.jpg") center center no-repeat;
  background-size: cover;
}

.greet {
  width: 300px;
  position: relative;
  animation: greet .4s linear;
}

.greet__user {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 155px;
  background-color: #fff;
  padding-bottom: 20px;
  border-radius: 10px 10px 0 0;
}

.greet__photo {
  position: absolute;
  top: -70px;
  left: 50%;
  transform: translateX(-50%);
  width: 140px;
  height: 140px;
  border-radius: 50%;
  border: 2px solid rgba(114, 114, 114, 0.6);
  overflow: hidden;
}

.greet__img {
  width: auto;
  height: 100%;
  display: block;
  margin-left: -47px;
}

.greet__name {
  font-family: "firaMedium", sans-serif;
  font-size: 21px;
  text-align: center;
  max-width: 150px;
}

.greet__over {
  background-color: #33c1e3;
  padding: 20px 0;
  border-radius: 0 0 10px 10px;
}

.greet__btn {
  display: block;
  width: 140px;
  font-family: "firaBold", sans-serif;
  font-size: 14px;
  color: #fff;
  border: 2px solid #fff;
  padding: 8px 13px;
  border-radius: 20px;
  margin: 0 auto;
  background: linear-gradient(to right, #edd500 0%, #00e5ff 30%, #ff0094 60%, #ff9d00 90%);
  background-size: 200%;
  animation: hello-btn 6s infinite linear;
}

@keyframes hello-btn {
  0% {
    background-position-x: 0;
  }

  100% {
    background-position-x: 200%;
  }
}

@keyframes greet {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

.header {
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2);
}

.container__header {
  justify-content: space-between;
  align-items: center;
  padding: 9px 0;
}

.user__pic {
  width: 62px;
  height: 62px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid #727272;
  display: inline-block;
  vertical-align: middle;
}

.user__img {
  display: block;
  margin-left: -20px;
  width: auto;
  height: 100%;
}

.user__name {
  display: inline-block;
  vertical-align: middle;
  margin-left: 19px;
  max-width: 160px;
}

.connect {
  display: flex;
  align-items: center;
}

.contacts {
  position: relative;
  margin-left: 15px;
}

.contacts::after {
  content: '';
  display: block;
  width: 100%;
  height: 20px;
  position: absolute;
  top: 100%;
}

.contacts:hover .contacts__dropdown {
  left: 50%;
}

.contacts__button {
  font-family: "firaBold", sans-serif;
  color: transparent;
  background: linear-gradient(to right, #edd500 0%, #00e5ff 30%, #ff0094 60%, #ff9d00 90%);
  background-clip: text;
  background-size: 200%;
  animation: hello-btn 6s infinite linear;
  transition: color .3s;
}

.contacts__dropdown {
  position: absolute;
  top: 100%;
  left: -9999px;
  transform: translateX(-50%);
  margin-top: 14px;
  z-index: 10;
}

.contacts__list {
  border: 2px solid rgba(114, 114, 114, 0.1);
  border-radius: 15px;
  background-color: #fff;
}

.contacts__list::before {
  content: '';
  display: block;
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 6px 10px 6px;
  border-color: transparent transparent rgba(114, 114, 114, 0.1) transparent;
}

.contacts__list::after {
  content: '';
  display: block;
  position: absolute;
  top: -6px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 6px 10px 6px;
  border-color: transparent transparent #fff transparent;
}

.contacts__item {
  border-bottom: 2px solid rgba(114, 114, 114, 0.1);
  padding: 15px 20px;
}

.contacts__item:last-child {
  border: none;
}

.contacts__link {
  display: flex;
  align-items: center;
}

.contacts__link:hover .contacts__name {
  color: rgba(65, 65, 65, 0.6);
}

.contacts__link:hover .contacts__icon {
  color: rgba(65, 65, 65, 0.6);
}

.contacts__name {
  transition: .3s;
}

.contacts__icon {
  color: #33c1e3;
  margin-right: 9px;
  width: 19px;
  transition: .3s;
}

.social__list {
  display: block;
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #33c1e3;
  cursor: pointer;
  transition: .3s;
}

.social__list:hover {
  animation: none;
  width: 120px;
  border-radius: 20px;
}

.social__list:hover .social__first {
  opacity: 0;
}

.social__list:hover .social__item {
  opacity: 1;
}

.social__list:hover .vk {
  right: 50%;
  transform: translateX(50%);
}

.social__list:hover .fb {
  right: 100%;
  transform: translateX(100%);
}

@keyframes squ {
  0% {
    border-radius: 50%;
    transform: rotate(0deg);
  }

  50% {
    border-radius: 0;
    transform: rotate(360deg);
  }

  100% {
    border-radius: 50%;
    transform: rotate(0deg);
  }
}

.social__first {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #33c1e3;
  text-align: center;
  animation: squ 5s infinite;
}

.social__item {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  transition-property: right, opacity;
  transition-duration: .4s, 1s;
  opacity: 0;
}

.social__link {
  display: block;
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #9c9c9c;
  transition: .3s;
}

.social__link:hover {
  background-color: #5d99c3;
}

.social__icon,
.social__network {
  position: absolute;
  width: 15px;
  height: 15px;
  fill: #414141;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.portfolio {
  width: 100%;
  height: 100%;
  padding-top: 20px;
}

.portfolio__container {
  display: block;
}

.portfolio__title-icon {
  font-size: 35px;
}

.portfolio__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.portfolio__item {
  width: 31%;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  margin-bottom: 15px;
}

.portfolio__item:hover {
  animation: item-hover .4s ease-in-out;
}

.portfolio__item:hover .portfolio__title {
  color: #e85f61;
}

.portfolio__item:hover .portfolio__descr {
  color: #e85f61;
}

.portfolio__item:hover .portfolio__icon {
  fill: #e85f61;
}

.portfolio__item:hover .portfolio__type-name {
  color: #e85f61;
}

@keyframes item-hover-2 {
}

@keyframes item-hover {
  0% {
    transform: translateX(0);
  }

  20% {
    transform: translateX(5px);
  }

  40% {
    transform: translateX(-5px);
  }

  60% {
    transform: translateX(3px);
  }

  80% {
    transform: translateX(-3px);
  }

  100% {
    transform: translateX(0);
  }
}

.portfolio__link {
  display: block;
}

.portfolio__block {
  padding: 17px 20px;
}

.portfolio__photo {
  min-height: 170px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  transition: .4s;
}

.portfolio__title {
  display: block;
  font-size: 21px;
  color: #33c1e3;
  line-height: 21px;
  transition: .4s;
}

.portfolio__descr {
  display: inline-block;
  font-family: "firalight", sans-serif;
  font-size: 14px;
  margin-bottom: 15px;
  transition: .4s;
}

.portfolio__icon {
  height: 18px;
  width: 20px;
  fill: rgba(114, 114, 114, 0.5);
  transition: .4s;
}

.portfolio__type-name {
  font-family: "firaBold", sans-serif;
  color: rgba(114, 114, 114, 0.5);
  transition: .4s;
}

@media screen and (max-width: 500px) {
  .container__header {
    flex-direction: column;
  }

  .user {
    margin-bottom: 15px;
  }

  .portfolio {
    text-align: center;
  }

  .portfolio__list {
    flex-direction: column;
  }

  .portfolio__item {
    width: 100%;
  }
}