html, body {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
	font-family: $firaRegular;
	font-size: 18px;
	color: $black;
	line-height: 1.42;
}

ul, li {
	margin: 0;
	padding: 0;
}

ul {
	list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0px;
  margin: 0px; 
}

p {
	margin: 0;
}

* {
	box-sizing: border-box;
}

a {
	color: $black;
	text-decoration: none;
	outline: none;
}

.wrapper {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.container {
	width: 96%;
	max-width: 940px;
	margin: 0 auto;
	display: flex;
}

.content {
	background-color: #f6f6f6;
	height: 100%;
}

.title {
	display: inline-block;
	font-family: $firaMedium;
	font-size: 35px;
	margin-bottom: 25px;
}

