.portfolio {
	width: 100%;
	height: 100%;
	padding-top: 20px;

	@include phone {
		text-align: center;
	}
}

.portfolio__container {
	display: block;
}

.portfolio__title-icon {
	font-size: 35px;
}

.portfolio__list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	width: 100%;

	@include phone {
		flex-direction: column;
	}
}

.portfolio__item {
	width: 31%;
	box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2);
	border-radius: 10px;
	margin-bottom: 15px;

	@include phone {
		width: 100%;
	}



  	&:hover {

  		animation: item-hover .4s ease-in-out;

  		.portfolio__title {
  			color: $red;
  		}

  		.portfolio__descr {
  			color: $red;
  		}

  		.portfolio__icon {
  			fill: $red;
  		}

  		.portfolio__type-name {
  			color: $red;
  		}
  	}
}

@keyframes item-hover-2 {

}

@keyframes item-hover {
	0% {
		transform: translateX(0);
	}

	20% {
		transform: translateX(5px);
	}

	40% {
		transform: translateX(-5px);
	}

	60% {
		transform: translateX(3px);
	}

	80% {
		transform: translateX(-3px);
	}

	100% {
		transform: translateX(0);
	}
}

.portfolio__link {
	display: block;
}

.portfolio__block {
	padding: 17px 20px;
}

.portfolio__photo {
	min-height: 170px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	transition: .4s;
}

.portfolio__title {
	display: block;
	font-size: 21px;
	color: $blue;
	line-height: 21px;
	transition: .4s;
}

.portfolio__descr {
	display: inline-block;
	font-family: $firaLight;
	font-size: 14px;
	margin-bottom: 15px;
	transition: .4s;
}

.portfolio__icon {
	height: 18px;
	width: 20px;
	fill: rgba($gray, .5);
	transition: .4s;
}

.portfolio__type-name {
	font-family: $firaBold;
	color: rgba($gray, .5);
	transition: .4s;
}